body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

h2 {
  margin: 0;
  padding: 0;
}

* {
  scroll-behavior: smooth;
}

.app {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: nowrap;
  height: 100vh;
  background: linear-gradient(90deg, rgb(255, 245, 60) 0%, rgba(252, 186, 14, 1) 100%);
}

.navbar {
  background-color: blueviolet;
}

.calculator {
  display: flex;
  flex-direction: column;
  border: 4px solid #929090;
}

.result {
  grid-column: 1 / -1;
  background-color: #929090;
  padding: 10px;
  text-align: right;
  color: #fff;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto repeat(4, 1fr);
  grid-gap: 2px;
  gap: 2px;
  background-color: #ddd;
  border: 1px solid #ccc;
  padding: 5px;
  width: 35vh;
}

.btn-text {
  grid-column: span 1;
  grid-row: span 1;
  background-color: #eee;
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
  font-weight: 500;
  font-size: 16px;
}

.btn-text:hover {
  background-color: rgb(174, 255, 228);
}

.btn-0 {
  grid-column: span 2;
  grid-row: span 1;
}

.orange {
  background-color: #ff7b23;
}

.quotesContainer {
  width: 40vw;
  height: 28vh;
}

.quoteItem {
  font-style: italic;
}

.author {
  text-align: right;
}

.loadingContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20vh;
}

.loading {
  border: 10px solid #fff; /* Light grey */
  border-top: 10px solid #44c510; /* Blue */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  -webkit-animation: spin 1.5s linear infinite;
          animation: spin 1.5s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

